.App {
	width: 100vw;
	height: 100vh;
	overflow-x: hidden;
	overflow-y: auto;
	display: flex;
	justify-content: center;
	align-items: center;
}

section {
	text-align: center;
	width: 720px;

	img {
		height: 200px;
		width: auto;
	}

	h1 {
		font-size: 3rem;
		margin-bottom: 15px;
	}

	h2 {
		font-size: 2rem;
	}

	h3 {
		font-size: 1rem;
		color: rgba(0, 0, 0, 0.7);
		margin-bottom: 30px;
	}

	.response-buttons {
		button {
			padding: 7px 20px;
			font-size: 18px;
			border-radius: 5px;
			border: 1px solid transparent;
			background: white;
			box-shadow: 0px 8px 32px 0px rgba(0, 0, 0, 0.1);
			cursor: pointer;

			&:not(:last-of-type) {
				margin-right: 30px
			}
		}
	}
}